nicze. Gwn zalet wczesnej diagnostyki jest posiadanie penej informacji co do rozwoju zgryzu oraz wiadomo co do potrzeb leczniczych.")])]),_vm._v(" "),_c('p',[_vm._v("\n        W przypadku wad i odchyle takich jak: zgryz krzyowy, otwarty, tyozgryz, stoczenie zbw, rozpoczyna si ortodontyczne leczenie wczesne, ktre pozwala na harmonijne ukierunkowanie wzrastajcych zbw i struktur twarzy. Wczesne leczenie u ortodonty jest czasami niezbdne i moe prawidowo przygotowa miejsca dla wyrzynajcych si zbw staych, tym samym mona unikn koniecznoci usuwania zbw staych w przyszoci. Leczenie ma rwnie na celu pobudzanie lub hamowanie wzrastania grnej i dolnej szczki. Pomaga to skorygowa dysproporcje w ich budowie i ksztacie, wyleczy rozbienoci ich wzajemnego pooenia, co w efekcie pozwala poprawi wygld rysw twarzy. Ze wzgldu na to, e u niektrych dzieci utrzymuje si nawykowe ssanie kciuka lub wargi dolnej, nie do koca prawidowy sposb poykania, problemy z wymow lub oddychanie wycznie przez usta, warto wysa dziecko do ortodonty w celu wczesnego skorygowania wymienionych wad. Wczesne leczenie ortodontyczne znacznie uatwia pniejsze leczenie w wieku modzieczym, tak aby byo moliwie krtkie i nie wymagao usuwania zbw i innych zabiegw chirurgicznych. W przypadku powanych wad u osb z rozbienoci w pooeniu szczk niepodjcie wczesnego leczenia powoduje, e efekty pniejszej kuracji mog by kompromisowe i niestabilne.\n      ")]),_vm._v(" "),_c('p',[_c('strong',[_vm._v("Fluoryzacja zbw")])]),_vm._v(" "),_c('p',[_vm._v("Fluoryzacja zbw mlecznych to zabieg szybki, bezbolesny i w peni bezpieczny. Zabieg trwa kilka minut. Na zbki Malucha za pomoc mikkiej, jednorazowej yki lub szczoteczki, nakadamy piank stomatologiczn o duej zawartoci fluoru i przyjemnym, owocowym smaku. Fluor przenika do szkliwa  w ten sposb wzmacnia jego struktur i czyni odporniejszym na kwasy, produkowane przez bakterie pytki nazbnej. Istnieje bdne przekonanie, i fluoryzacja zbw u Dzieci bardziej szkodzi, ni pomaga. Tymczasem z fluorem jest podobnie jak z nitrogliceryn  z jednej strony uznawana jest za substancj toksyczn, stosowan do produkcji dynamitu, z drugiej za ratuje ludzkie ycie, pomagajc chorym na choroby niedokrwienne serca. Najwaniejsze jest tutaj dawkowanie i waciwa procedura podania. Fluoryzacja zbw jest zabiegiem w peni bezpiecznym. Fluor w postaci pianki, aplikowany na zby, nie zagraa zdrowiu Malucha, a chroni jego umiech przed prchnic, czego dowodem s nasi zdrowi Mali Pacjenci. Fluorowanie zapewnia dodatkow ochron przed rozwojem prchnicy, co jest szczeglnie istotne w przypadku Najmodszych Pacjentw, gdy nieatwo o utrzymanie dokadnej, prawidowej higieny jamy ustnej.")]),_vm._v(" "),_c('p',[_c('strong',[_vm._v("Lakowanie zbw")])]),_vm._v("\n      Bruzdy i szczeliny zbowe s miejscami trudno dostpnymi  bardzo trudno jest je oczyci podczas codziennego szczotkowania zbw. Std w bruzdach szybko pojawia si prchnica. Lakowanie to metoda polegajca na uszczelnianiu bruzd. Stosuje si j gwnie u osb modych, gdzie bruzdy zbw trzonowych lub przedtrzonowych nie zostay jeszcze zaatakowane przez prchnic. Lak chroni bruzdy przed szkodliwym dziaaniem czynnikw zewntrznych (np. cukrem). Powinnimy uszczelnia zby naszych maluchw jak najszybciej po wyrzniciu caej powierzchni ujcej. Przed zabiegiem lakowania bardzo dokadnie oczyszczamy zb piaskark lub szczoteczk ze specjaln past. Oczyszczone i osuszone powierzchnie badamy laserem diagnostycznym, ktry wykrywa nawet bardzo wczesn prchnic, niewidoczn goym okiem. Zastosowanie tego urzdzenia pozwala jednoznacznie zakwalifikowa zby do lakowania i wykry pocztkow, niewidoczn demineralizacj szkliwa, ktra jest przeciwwskazaniem do lakowania. Lakowanie to bezbolesna metoda zabezpieczania zbw mlecznych.\n\n      "),_c('p',[_c('strong',[_vm._v("Ozonoterapia zbw")])]),_vm._v(" "),_c('p',[_vm._v("Zby mleczne rni si od zbw staych anatomi. Ich saba mineralizacja sprawia, e rozwj prchnicy nastpuje byskawicznie i w krtkim czasie moe doprowadzi do zniszczenia caych koron zbowych. By temu zapobiec, stosujemy bezpieczny, krtki i w peni bezbolesny zabieg: ozonowanie zbw. Ozon charakteryzuje si silnym dziaaniem bakteriobjczym. Usuwa nawet 99,9% bakterii, odpowiedzialnych za rozwj prchnicy  dziki temu hamuj j, pozwalajc na zachowanie mleczakw do momentu pojawienia si uzbienia staego. Zatrzymanie prchnicy w pocztkowym stadium eliminuje ryzyko powstania gbokich zmian, prowadzcych do destrukcji zbw i ich usunicia. To bardzo wane, by proces formowania si zgryzu przebiega prawidowo. Przyczyn zmian prchnicotwrczych, powstajcych w zbach s przede wszystkim bakterie, zasiedlajce jam ustn. W korzystnych dla siebie warunkach (np. w bruzdach zbowych), bakterie metabolizuj cukry zawarte w pokarmach. Produktem ubocznym tego procesu s kwasy, ktre niszcz szkliwo i zbin  w przypadku mleczakw, byskawicznie! Dlatego tak istotne jest wczesne rozpoznanie zmian prchnicowych. Przebieg ozonowania u Dzieci trwa zaledwie 20 sekund! To metoda w peni akceptowana przez Maych Pacjentw. Podczas zabiegu, ktry jest cakowicie bezbolesny, nie stosujemy adnych wierte, nie borujemy. Na zb ze zmianami prchnicowymi zakadamy kapturek, poczony z urzdzeniem (ozonatorem), wytwarzajcym leczniczy gaz. Po zabiegu stosujemy dodatkowo preparaty remineralizujce.")]),_vm._v(" "),_c('p',[_c('strong',[_vm._v("Sedacja wziewna")])]),_vm._v(" "),_c('p',[_vm._v("Za pomoc mieszaniny zoonej z N2O (zwanego take gazem rozweselajcym) oraz tlenu. Zastosowanie wspomnianej mieszaniny gazw pozwala Pacjentowi na zrelaksowanie si w czasie zabiegu. Osoba nie odczuwa blu w czasie zabiegu, Pacjent nie odczuwa te stresu. Mieszanina gazw podawana jest za pomoc maski nosowej (sprzedajemy maski wielokrotnego uytku).")]),_vm._v(" "),_c('p',[_vm._v("Sedacja wziewna jest wskazana u osb, ktre odczuwaj paniczny strach przed jakimkolwiek zabiegiem stomatologicznym, nawet w czasie zabiegw, ktre nie wi si z ryzykiem duego blu. Ponadto w czasie dugotrwaych i mao przyjemnych zabiegw, zwaszcza u Pacjentw z silnym odruchem wymiotnym 